
import bgSignUp from '../images/signup.png';
import Footer from '../components/Footer'
import {Link} from 'react-router-dom';
import React from 'react';

function SignUp(){
    return(
        <div>
            <div className="w-full h-screen flex justify-center items-center px-20 lg:px-40">
                <div className="w-screen h-96 rounded-lg border-2 border-gray-300 shadow-xl flex flex-col lg:flex-row md:flex-row">
                    <div className="left-side w-full h-full bg-center bg-no-repeat bg-cover font-poppins flex flex-col items-center rounded-tl-lg rounded-tr-lg lg:rounded-bl-lg md:rounded-bl-lg lg:rounded-tr-none md:rounded-tr-none" style={{backgroundImage: `url(${bgSignUp})`}}>
                       <div className="text-gray-50 w-full mt-2 ml-6 lg:ml-12 md:ml-12 lg:mt-4 md:mt-4">
                            <Link to={'/'}>
                                <i className="fa fa-arrow-left text-left cursor-pointer hover:text-gray-300"></i>
                            </Link>
                       </div>
                        <h1 className="text-gray-100 h-full text-sm lg:text-2xl md:text-xl">Create an Account</h1>
                        <div className="w-52 h-full flex flex-col text-gray-50 text-xs lg:text-calc-1 px-2 lg:pt-20 md:pt-20">
                            <button className="outline-none p-1 border-2 border-gray-50 rounded-md hover:bg-blue-700 hover:transition ease-in-out duration-200"><i className="fa fa-user-o"></i>&nbsp;&nbsp;Sign-up as teacher</button>
                            <button className="outline-none p-1 border-2 border-gray-50 rounded-md mt-2 hover:bg-blue-700 hover:transition ease-in-out duration-200"><i className="fa fa-user"></i>&nbsp;&nbsp;Sign-up as student</button>
                        </div>
                    </div>
                    <div className="right-side w-full h-full p-6">
                       <div className="teacher-sign-up w-auto h-auto bg-gray-100 p-2 border-2 border-gray-400 relative">
                           
                       </div>
                       <div className="student-sign-up hidden">

                       </div>
                    </div>
                </div>
            </div>
            <Footer/>
        </div>

    );
}

export default SignUp;